.Login {
    width: 1080px;
    /* width: 100%; */
    margin: 15px auto 0;
}

.Login .l_Center, .Login .u_m-title, .Login body {
    position: relative;
}

.Login .news-con {
    zoom: 1;
    overflow: hidden;
    padding: 20px;
}

.Login .news_rewrapper {
    width: 700px;
    margin: 0 auto;
    border: 1px solid #eee;
    border-radius: 10px;
    padding: 20px 43px 32px 35px;
    background-color: white;
    box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12) !important;
    border-radius: 5px;
}

.Login a:link, .Login a:visited {
    color: #555;
}

.Login a:hover {
    color: red;
}

.Login a, .Login a:link, .Login a:visited {
    text-decoration: none;
}

.Login .news-recommend-list {
    background-color: white;
}

.Login .u_m-title {
    margin-bottom: 30px;
    position: relative;
    line-height: 20px;
}

.Login .u_m-title h2, .Login .u_m-title h3 {
    font-size: 20px !important;
    font-weight: 800;
    color: #000;
    border-left: 3px solid #f56f4a;
    padding-left: 4px;
}

.Login .u_m-title a {
    font-size: 14px;
}

.Login .news-recommend-list {
    font-size: 16px;
    line-height: 33px;
    margin-top: 8px;
    width: 585px;
}

.Login .form-horizontal .control-label {
    padding-top: 10px;
    margin-bottom: 0;
    text-align: right;
}

.Login label {
    display: inline-block;
    max-width: 100%;
    margin-bottom: 5px;
    font-weight: 700;
}

.Login label {
    cursor: pointer;
}

.Login .col-sm-10 {
    width: 60%;
}

.Login .form-control {
    display: block;
    width: 100%;
    height: 34px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
    -webkit-transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
    -o-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
}

.Login .form-horizontal .control-label {
    padding-top: 10px;
    margin-bottom: 0;
    text-align: right;
}

.Login .news-recommend-list a {
    display: inline-block;
    width: 406px;
    overflow: hidden;
    margin-top: 10px;
}

.Login .aside .side_list li .item strong, .Login .news-recommend-list a {
    white-space: nowrap;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
}

.Login .code_png {
    cursor: pointer;
    width: 70px;
}

.Login .news-recommend-list a {
    display: inline-block;
    width: 406px;
    overflow: hidden;
}

.Login .u_m-title h2 a {
    margin-left: 10px;
    font-weight: 700;
}

.Login .errorMessage_show {
    display: flex;
}

.Login .MuiAlert-message {
    font-size: 16px;
    font-weight: 600;
}

.Login .news-recommend .MuiAlert-message {
    color: red;
}

.Login .errorMessage_hide {
    display: none;
}

.Login form .passwordgroup {
    margin-bottom: 0px !important;
}

.Login form .form-group {
    display: inline-flex;
    margin-bottom: 20px;
}

.Login form .form-group .form-label {
    width: 100px;
    float: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 16px;
}

.Login form .form-group input {
    height: 35px;
    font-size: 18px;
}

.Login form .form-group .form-control {
    float: left;
    width: 400px;
}

.Login .loginbtn {
    margin-left: 115px;
}

.Login .guestbtn {
    margin-left: 20px;
    background-color: orange;
}

.Login .guestbtn:hover {
    background-color: orangered;
}

.Login .loginbtn .MuiButton-label, .Login .guestbtn .MuiButton-label {
    font-size: 18px;
    font-weight: 600;
}

.Login form .form-group .yzm_input {
    width: 250px;
}

.Login form .form-group .yzm_img {
    width: 130px;
    margin-left: 20px;
    height: 35px;
}

.Login form .form-group .forgotpassword {
    margin-top: 0px;
    margin-left: 100px;
}