.newspage {
    margin-top: 10px !important;
}

.newspage .news-main .news-nav a:hover {
    color: red;
    text-decoration: none;
}

.newspage .l_Center {
    width: 1080px;
    margin: 0 auto;
}

.newspage .l_Center, .u_m-title, body {
    position: relative;
}

.newspage .aside {
    font: 12px/1.2em Microsoft YaHei, "\5FAE\8F6F\96C5\9ED1", SimHei, "\9ED1\4F53", Arial;
    background-color: transparent;
    color: #000;
}

.newspage .aside, .aside-nav {
    float: left;
    display: block;
    width: 25%;
}

.newspage .aside .news-qs {
    margin-bottom: 10px;
    background-color: white;
}

.newspage .aside .side_title_faq {
    height: 48px;
    border-left: 3px solid #464859;
    background-color: #464859;
    background-position: 20px -255px;
    color: #fff;
    display: block;
}

/* .newspage .news .icon {
    background: url(../images/icon.807c7410.png) no-repeat;
} */

.newspage .icon {
    overflow: hidden;
    vertical-align: middle;
}

.newspage .icon, .icon_block {
    background-repeat: no-repeat!important;
}
