.newspage .aside .side_title_faq h3 {
    background: url(../images/icon.807c7410.png) no-repeat;
    margin: 0;
    font: normal 700 16px/48px 'Microsoft Yahei', Arial, SimSun, '\5b8b\4f53', sans-serif;
    padding: 0 0 0 46px;
} */

.newspage h3 {
    display: block;
    /* font-size: 1.17em; */
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    /* font-weight: bold; */
}

.newspage .aside .side_title_faq {
    height: 48px;
    border-left: 3px solid #464859;
    background-color: #464859;
    background-position: 20px -255px;
    color: #fff;
    display: block;
}

.newspage .aside .side_wrap {
    background-color: white;
}

.newspage .aside .side_list_faq, .newspage .aside .side_wrap, .newspage .news .news_list .list {
    border: 1px solid #eee;
    zoom: 1;
    overflow: hidden;
}

.newspage ul {
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}

.newspage ol, ul {
    list-style: none;
}

.newspage li {
    display: list-item;
    text-align: -webkit-match-parent;
}

.newspage .aside .side_list_faq a:link, .newspage .aside .side_list_faq a:visited {
    color: #f56f4a;
}

.newspage a, a:link, a:visited {
    text-decoration: none;
}

.newspage a:-webkit-any-link {
    cursor: pointer;
}

.newspage .aside .side_list_faq li {
    height: 21px;
    overflow: hidden;
    color: #f56f4a;
}

.newspage .aside .news-recommend {
    width: 85%;
    padding: 17px 20px;
}

.newspage .news-recommend {
    float: left;
}

.newspage .news-recommend .u_m-title {
    font-size: 18px;
    line-height: 20px;
}

.newspage .u_m-title {
    margin-bottom: 15px;
    position: relative;
}

.newspage .u_m-title h2, .u_m-title h3 {
    font-size: 16px;
    font-weight: 800;
    color: #000;
    border-left: 3px solid #f56f4a;
    padding-left: 4px;
}

.newspage .aside .side_list li {
    padding: 5px 0 0;
    overflow: hidden;
}

.newspage .aside .side_list li .item {
    width: 100%;
    overflow: hidden;
    padding: 8px 0;
    text-align: center;
}

.newspage .aside .side_list li .item em {
    width: 170px;
    display: block;
    margin: 0 auto;
    float: none;
    font-style: normal;
}

.newspage a, a:link, a:visited {
    text-decoration: none;
}

.newspage .aside .side_list_faq {
    padding: 20px;
}

.newspage .aside .side_list li .item strong {
    float: none;
    display: block;
    margin-top: 5px;
    font-weight: lighter;
    line-height: 16px;
    margin-bottom: 10px;
    overflow: hidden;
}

.newspage .aside .side_list li .item strong, .news-recommend-list a {
    white-space: nowrap;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
}

.newspage .aside .side_list li .item strong, .news-recommend-list a {
    white-space: nowrap;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
}

.newspage .news-main {
    width: 100%;
    background-color: white;
    padding: 30px;
    box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12) !important;
    border-radius: 5px;
    padding-bottom: 15px;
}

.newspage .news .news_list {
    width: 100%;
}

.newspage .news .news_list .list {
    padding: 10px 25px;
    box-shadow: none !important;
}

.newspage .aside .side_list_faq, .aside .side_wrap, .news .news_list .list {
    border: 1px solid #eee;
    zoom: 1;
    overflow: hidden;
    box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12) !important;
    border-radius: 5px;
}

.newspage .news-nav {
    height: 30px;
    line-height: 20px;
    overflow: hidden;
    margin: 10px 10px;
}

.newspage .news-nav .c_name {
    color: #f56f4a;
}

/* .newspage .news_list {
    margin-top: 10px;
    margin-bottom: 10px;
} */

.newspage .news .news_list h2 {
    margin: 0;
    padding: 0;
    font: normal 700 19px/60px 'Microsoft Yahei', Arial, SimSun, '\5b8b\4f53', sans-serif;
}

.newspage .news .news_list .list {
    padding: 10px 25px;
}

.newspage .news .news_list .list ul {
    width: 100%;
    overflow: hidden;
    color: #ff7954;
}

.newspage .news .news_list .list ul a {
    font-size: 16px;
    color: black !important;
}

.newspage .news .news_list .list ul a:hover {
    text-decoration: none;
    color: #ff7954;
}

.newspage .news .news_list .list li {
    height: 44px;
    line-height: 44px;
    border-bottom: 1px dotted #d8dfe5;
    align-items: center;
}

.newspage .news .news_list .list li:last-child {
    border-bottom: none;
}

.newspage .news .pagenav {
    width: 100%;
    overflow: hidden;
    clear: both;
    text-align: center;
    margin-top: 32px;
}

.newspage select:not(:-internal-list-box) {
    overflow: visible !important;
}

.newspage select {
    text-rendering: auto;
    color: -internal-light-dark-color(black, white);
    letter-spacing: normal;
    word-spacing: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    display: inline-block;
    text-align: start;
    -webkit-appearance: menulist;
    box-sizing: border-box;
    align-items: center;
    white-space: pre;
    -webkit-rtl-ordering: logical;
    background-color: -internal-light-dark-color(white, black);
    cursor: default;
    margin: 0em;
    font: 400 13.3333px Arial;
    border-radius: 0px;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(169, 169, 169);
    border-image: initial;
}

.newspage .news .news_list .list span {
    float: right;
    color: #ccc;
    margin-top: 15px;
    font-size: 14px;
}

.newspage .giftcent {
    margin: 0 16px;
    width: 420px;
}

.newspage .billborad-money {
    width: 100px;
    color: #ff0000;
}

.newspage .billboard-no2 .billborad-order {
    background: #f84e31;
}

.newspage .billboard-no3 .billborad-order {
    background: #31f89b;
}

.newspage .side_wrap {
    box-sizing: unset;
}

.newspage .news_pagination ul {
    display: inline-flex;
}

.news_pagination nav {
    text-align: center;
}

.newspage .news_pagination .MuiPagination-root .MuiPagination-ul .Mui-selected {
    background-color: white !important;
}