.game_centre .mode {
    margin: 0 auto;
    width: 100%;
    padding: 10px;
    border: 2px solid #FDCD77;
    text-align: center;
    border-radius: 5px;
}

.game_centre .mode .MuiButton-label {
    font-size: 14px;
    font-weight: 600;
}

.game_centre .mode ul {
    padding: 0 9px;
}

.game_centre .mode ul li {
    width: 43px;
    height: 21px;
    border: 1px solid #fdcd77;
    float: left;
    text-align: center;
    line-height: 21px;
    margin: 0 4px 5px 0;
}

.game_centre .mode .gpeilu {
    clear: none;
    float: left;
}

.game_centre .mode .gpeilu li {
    border: 1px solid #E6AFDD !important;
}

.game_centre .mode .gpeilu .peilu {
    width: 70px !important;
}

.game_centre .times {
    margin: 10px 0px 20px 0;
    width: 100%;
    display: inline-block;
}

.game_centre .times .chipsul {
    float: left;
    margin-right: 5px;
}

.game_centre .times .chipsul li {
    float: left;
}

.game_centre .times .chipsul li.chips a {
    width: 62px;
    height: 31px;
    display: block;
    float: left;
    text-decoration: none;
}

.game_centre .times .self {
    height: 29px;
    line-height: 29px;
    border: 2px solid #FDCD77;
    float: left;
    background: #FFF5C7;
    color: #8B5100;
}

.game_centre .times .self li {
    float: left;
    height: 29px;
    line-height: 29px;
    font-size: 13px;
}

.game_centre .times .self .avinput {
    border: none;
    height: 24px;
    width: 75px;
    color: #f00;
    font-size: 16px;
    padding-left: 2px;
}

.game_centre .times .self li a {
    float: left;
    width: 74px;
    height: 29px;
    background: url(../images/total.fw.png) no-repeat center;
    color: #fff;
    text-align: center;
    font-size: 14px;
    text-indent: 0px;
}

