.betDialog .MuiDialogTitle-root {
    padding: 0px !important;
}

.betDialog .MuiDialogTitle-root h2 {
    display: inline-block;
    width: 100%;
    padding: 10px;
    border-bottom: 2px solid #eee;
}

.betDialog .MuiDialogTitle-root h2 p {
    margin-bottom: 0px;
    font-size: 20px;
    font-weight: 600;
    line-height: 41px;
    display: inherit;
}

.betDialog .MuiIconButton-root {
    float: right;
}

.betDialog .secondtitle {
    font-size: 18px;
    padding: 10px;
    font-weight: 600;
    border-bottom: 2px solid #eee;
}

.betDialog .dialogcontent {
    padding: 10px;
}

.betDialog .dialogcontent table {
    width: 100%;
}

.betDialog .dialogcontent table thead tr th {
    font-size: 16px;
    font-weight: 600;
    text-align: center;
    border: 1px solid #ddd;
    padding: 5px 0px;
}

.betDialog .dialogcontent table tbody tr td {
    font-size: 16px;
    font-weight: 600;
    text-align: center;
    border: 1px solid #ddd;
    padding: 5px 0px;
}

.betDialog .dialogcontent table tbody tr:nth-child(even) {
    background-color: #ccc;
}

.betDialog .dialogcontent .bet {
    display: flex;
    justify-content: center;
}

.betDialog .dialogcontent .betNum {
    background: url(../images/bethistory_result.png);
    background-size: 30px 30px;
    width: 30px;
    height: 30px;
    color: white;
    font-size: 15px;
    line-height: 30px;
    font-weight: 600;
}