.profile_page {
    float: right;
    width: 800px;
    border: 1px;
    padding: 20px 40px 30px 40px;
    border: 2px solid #eee;
}

.profile_page .input_form {
    padding: 10px;
    display: inline-flex;
    align-items: center;
    width: 100%;
}

.profile_page .MuiOutlinedInput-input, .profile_page .MuiFilledInput-input  {
    padding: 10px;
    width: 300px;
}

.profile_page .input_form_text {
    display: inline-flex !important;
    font-size: 16px;
    font-weight: 600;
    align-items: center;
}

.profile_page .input_content {
    font-size: 16px;
    margin-left: 20px;
    font-weight: 600;
}

.profile_page .QQ_text3 {
    margin-left: 30px !important;
}

.profile_page .middle_divider {
    border: 1px dotted;
    margin-top: 50px;
    margin-bottom: 15px;
}

.profile_page .sub_title {
    font-size: 18px;
    font-weight: 600;
    border-left: 5px solid red;
    padding-left: 15px;
}

.profile_page .sign {
    font-size: 18px;
    font-weight: 600;
    margin: 0px;
    line-height: 35px;
    width: 60px;
}

.profile_page .updateBtn {
    padding: 10px 20px;
    margin-top: 20px;
    margin-left: 105px;
}

.profile_page .updateBtn .MuiButton-label {
    font-size: 16px;
    font-weight: 600;
}

.profile_page .emailcheckBtn {
    margin-left: 30px;
}

.profile_page .emailcheckBtn .MuiButton-label {
    font-size: 16px;
}