.News_detail {
    width: 800px;
font: 16px
}

.News_detail .l_Center {
    margin: 15px auto 0;
}
.News_detail .l_Center, .u_m-title, body {
    position: relative;
}
.News_detail .news-nav {
    height: 30px;
    line-height: 20px;
    overflow: hidden;
}
.News_detail a:link, .News_detail a:visited {
    color: #555;
}
.News_detail a, .News_detail a:link, .News_detail a:visited {
    text-decoration: none;
}
.News_detail .news-con {
    zoom: 1;
    overflow: hidden;
    border: 1px solid #eee;
    padding: 20px 30px;
    background-color: white;
    box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12) !important;
    border-radius: 5px;
}   
.News_detail .news #NIE-art {
    text-align: center;
    width: 100%;
    position: relative;
}
.News_detail .news #NIE-art .artTitle {
    margin: 0;
    padding: 0;
    font: normal 700 19px/2em 'Microsoft Yahei',Arial,SimSun,'\5b8b\4f53',sans-serif;
    text-align: center;
}
.News_detail h1 {
    display: block;
    font-size: 2em;
    margin-block-start: 0.67em;
    margin-block-end: 0.67em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
}
.News_detail .news #NIE-art .artInfo {
    height: 25px;
    clear: both;
    text-align: center;
}

.News_detail p {
    display: block;
    /* margin-block-start: 1em; */
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}
.News_detail .news #NIE-art .artInfo .artDate {
    height: 35px;
    font: italic 400 14px/21px Arial,SimSun,'\5b8b\4f53',sans-serif;
    color: #c9c9c9;
    border-bottom: 1px dotted #d8dfe5;
    /* padding: 0 20px 0 0; */
    /* text-align: center; */
    }
.News_detail .news #NIE-art .artText {
    font: 14px/24px SimSun,'\5b8b\4f53',sans-serif;
}
.News_detail .news_rewrapper {
    margin-top: 15px;
    /* margin-bottom: 10px; */
    border: 1px solid #eee;
    /* padding: 33px 43px 0px 35px; */
    padding: 20px;
    float: left;
    /* margin-left: 30px; */
    width: 100%;
}
.News_detail .news-recommend {
    float: left;
    width: 100%;
}
.News_detail .news-recommend .u_m-title {
    font-size: 18px;
    line-height: 20px;
}
.News_detail .u_m-title {
    margin-bottom: 15px;
    position: relative;
    line-height: 18px;
}
.News_detail .u_m-title h2, .News_detail .u_m-title h3 {
    font-size: 18px;
    font-weight: 800;
    color: #000;
    border-left: 3px solid #f56f4a;
    padding-left: 4px;
}

.News_detail .news-recommend-list {
    font-size: 16px;
    line-height: 33px;
    margin-top: 8px;
    width: 100%;
}

.News_detail  .news-nav a:hover{
    color: red;
    text-decoration: none;
}

.News_detail ol, .News_detail ul {
    list-style: none;
}

.News_detail li {
    display: list-item;
    text-align: -webkit-match-parent;
    margin-bottom: 10px;
}

.News_detail .news-erwei, .News_detail .news-recommend-list .time {
    float: right;
    font-size: 14px;
}
.News_detail .news-recommend-list a {
    display: inline-block;
    width: 406px;
    overflow: hidden;
}
.News_detail .news-recommend-list a:hover{
    color: tomato;
}

.News_detail .aside .side_list li .item strong, .News_detail .news-recommend-list a {
    white-space: nowrap;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
}
.News_detail a:link, .News_detail a:visited {
    color: #555;
}
.News_detail a, .News_detail a:link, .News_detail a:visited {
    text-decoration: none;
}
.News_detail .news-recommend-list a {
    display: inline-block;
    width: 406px;
    height: 30px;
    overflow: hidden;
}

.News_detail .MuiButton-text {
    width: 100%;
}

.News_detail .MuiButton-label {
    font-size: 16px;
    text-align: left;
    display: inline-block;
}