.nav-link {
    /* width: 100px; */
    font-weight: 900;
    font-size: 18px;
    margin-right: 15px;
    margin-left: 15px;
    /* font: normal 700 20px/50px 'Microsoft Yahei', Arial, SimSun, '\5b8b\4f53', sans-serif; */
}

.navbar {
    background-color: #4267b2 !important;
    border-bottom: 2px solid #29487d;
}

.top_bar {
    width: 1080px;
    margin: 0 auto;
    position: relative;
    color: white;
    padding: 10px !important;
    height: 40px;
    /* background: rgba(0, 0, 0, 0.5); */
}

.background_black {
    background-color: rgba(0,0,0, 0.8);
}

.top_bar .time_shower {
    float: left;
}

.top_bar .validated_name {
    float: left;
    margin-left: 30px;
    color: #ffef00;
}

.top_bar .validated_amount {
    float: left;
    margin-left: 30px;
    color: white;
    /* font-weight: 600; */
}

.top_bar strong {
    color: #ffef00;
    font-size: 16px;
}

.header {
    width: 1080px;
    margin: 0 auto;
    position: relative;
    margin-bottom: 5px;
}

.header .Sign_in_out {
    float: right;
    height: 130px;
    align-items: center;
    /* margin-right: 50px;
    margin-top: 20px; */
    display: inline-flex;
}

.header .Sign_in_out .MuiButton-root {
    background-color: #007bff;
}

.header .Sign_in_out .top_menu_button {
    margin-left: 15px;
    background-color: #ffc107;
}

.header .Sign_in_out .MuiButton-label {
    font-size: 15px;
    color: white;
    float: left;
}

.header .logo {
    margin-left: 10px;
    float: left;
    padding: 20px;
}

.header .subhead {
    height: 55px;
    line-height: 55px;
    padding-left: 10px;
    font-size: 20px;
    color: #6a6c75;
    float: left;
}

.header .header-weixin {
    position: absolute;
    right: 0;
    top: -5px;
    z-index: 80;
    padding: 0 12px;
}

.navbar {
    width: 100%;
}

/* .header .gift_dropdown {
    background: turquoise;
    color: turquoise;
} */

.header .MuiButton-contained {
    box-shadow: none
}

/* @media (min-width: 300px){
    .navbar-expand-lg .navbar-nav {
        flex-direction: row !important;
    }
} */

@media (min-width: 0px) {
    .navbar-expand-lg .navbar-nav {
        flex-direction: row !important;
    }
}

.collapse:not(.show) {
    display: block !important;
}

.navbar-toggler {
    display: none !important;
}

.navbar-nav {
    flex-direction: row !important;
}

.dropdown-menu {
    position: absolute !important;
}

.Sign_in_out .topnav {
    text-align: center;
    margin-left: 20px;
}

.Sign_in_out .topnav:hover p {
    color: #007bff;
}

.Sign_in_out .topnav a {
    text-decoration: none;
}

.Sign_in_out .topnav img {
    width: 45px;
    height: 45px;
}

.Sign_in_out .topnav p {
    margin: 10px 0px 0px;
    font-size: 18px;
    font-weight: 900;
    color: black;
}

.Sign_in_out .tn_active p{
    color: #007bff;
}