.rechargecalc {
    margin-top: 10px;
    box-shadow: 0px 3px 5px -1px rgba(0,0,0,0.2), 0px 5px 8px 0px rgba(0,0,0,0.14), 0px 1px 14px 0px rgba(0,0,0,0.12) !important;
    background: white;
    padding: 20px;
}

.withdrawlcalc {
    margin-bottom: 20px;
}

.withdrawlcalc .MuiSnackbar-anchorOriginTopCenter {
    top: 45% !important;
}

.withdrawlcalc .MuiAlert-message {
    font-size: 20px;

}

.rechargecalc .main {
    display: inline-flex;
    width: 100%;
}

.rechargecalc .money {
    background-color: #FF9138;
    color: white;
    font-size: 40px;
    border-radius: 20px;
    padding: 50px 30px;
    width: 40%;
    margin-right: 30px;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.rechargecalc .content {
    background-color: #eeeeee;
    width: 60%;
    padding: 20px;
}

.rechargecalc .MuiOutlinedInput-input {
    padding: 10px 10px !important;
    font-size: 16px !important;
}

.rechargecalc .MuiOutlinedInput-notchedOutline {
    border-color: black;
}

.rechargecalc .content .title {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 20px;
}

.rechargecalc .content .goods_para {
    border-bottom: 1px solid #aaaaaa;
}

.rechargecalc .content .goods_para dl{
    width: 100%;
    line-height: 21px;
    display: inline-block;
}

.rechargecalc .content .goods_para p {
    margin-bottom: 0px;
    font-size: 15px;
}

.rechargecalc .content .goods_para dt {
    width: 100px;
    float: left;
    font-size: 16px;
}

.rechargecalc .content .goods_para dd {
    float: left;
    font-size: 16px;
}

.rechargecalc .content .goods_para .goods_pice {
    color: #fb6f49;
    font-size: 19px;
    padding-right: 3px;
}

.rechargecalc .content .goods_buy {
    margin-top: 20px;
    text-align: center;
    display: inline-flex;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
}

.rechargecalc .content .goods_buy_t {
    width: 100px;
    font-size: 18px;
    font-weight: 600;
}

.rechargecalc .content .goods_buy .MuiButton-label {
    font-size: 16px;
}

.rechargecalc .content .buyButton {
    background-color: orange;
    color: white;
}

.rechargecalc .content .buyButton .MuiButton-label {
    font-size: 16px;    
    padding: 5px 10px;
    font-weight: 600;
}

.rechargecalc .MuiAlert-message {
    font-size: 18px;
    font-weight: 600;
}