.activityarea {
    padding-bottom: 20px;
}

.activityarea .backhd {
    background: none repeat scroll 0% 0%;
}

.activityarea .box {
    position: relative;
}

.activityarea .boximage {
    width: 1080px;
    margin-top: 15px;
    height: 150PX;
}

.activityarea img {
    border: none;
    vertical-align: middle;
}

.activityarea .text {
    line-height: 22px;
    padding: 0 6px;
    color: #666;
    padding: 25px;
    background: #fff;
    border: 1px solid #ccc;
    /* display: none; */
    overflow: hidden;
    overflow-x: hidden;
    overflow-y: hidden;
}

.activityarea p {
    margin-top: 0;
    margin-bottom: 1rem;
}

.activityarea .text p span {
    display: block;
    line-height: 24px;
    color: #666;
    font-size: 14px;
}

.activityarea .text p span.act_cont_d, .text p span.act_obj_d {
    text-indent: 2em;
}

.activityarea .text .act_cont_tb {
    width: 100%;
    margin: 10px 0;
    background: #f56f4a;
}

.activityarea table {
    border-collapse: collapse;
}

.activityarea table {
    display: table;
    border-collapse: separate;
    border-spacing: 2px;
    border-color: grey;
}

.activityarea tbody {
    display: table-row-group;
    vertical-align: middle;
    border-color: inherit;
}

.activityarea tr {
    display: table-row;
    vertical-align: inherit;
    border-color: inherit;
}

.activityarea .text .act_cont_tb th, .text .act_cont_tb td {
    text-align: center;
    height: 30px;
    line-height: 30px;
    border: 1px solid #9A462F;
    color: #FFF;
}

.activityarea th {
    display: table-cell;
    vertical-align: inherit;
    font-weight: bold;
    text-align: -internal-center;
}

.activityarea .act_obj {
    color: red !important;
}

.activityarea .act_cont {
    color: red !important;
}

.activityarea .datatype1 {
    border: 1px solid #eee;
    background-color: white;
    padding-left: 30px;
    padding-bottom: 30px;
    width: 1080px;
    margin-top: 10px;
    box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12) !important;
    border-radius: 5px;
}

.activityarea pre {
    font: normal 500 15px 'Microsoft Yahei', Arial, SimSun, '\5b8b\4f53', sans-serif;
}