.Business_cooperation .l_Center {
    /* width: 1100px; */
    margin: 10px auto 0;
    display: block;
    float: left;
    background-color: white;
    /* padding: 10px; */
    border-radius: 5px;
    box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12) !important;
}

.Business_cooperation .l_Center, .Business_cooperation .u_m-title, .Business_cooperation body {
    position: relative;
}

.Business_cooperation .merchans_list {
    /* width: 770px !important; */
    float: left;
    margin-top: 30px;
    padding: 30px;
}

.Business_cooperation dl, .Business_cooperation ol, .Business_cooperation ul {
    margin-top: 0;
    margin-bottom: 1rem;
}

.Business_cooperation .newspage ol, .Business_cooperation ul {
    list-style: none;
}

.Business_cooperation .privilege-page-list .privilege-item {
    margin: 0 26px 20px;
    overflow: hidden;
}

.Business_cooperation .privilege-list .privilege-item, .Business_cooperation .privilege-page-list .privilege-item {
    float: left;
    margin-bottom: 41px;
    padding: 0;
    width: 205px !important;
    height: 188px;
    overflow: hidden;
    position: relative;
    zoom: 1;
}

.Business_cooperation .privilege-list .privilege-item a, .Business_cooperation .privilege-page-list .privilege-item a {
    display: block;
    width: 100%;
    height: 100%;
    text-decoration: none;
    cursor: pointer;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    overflow: hidden;
    -webkit-mask-image: -webkit-radial-gradient(#000, #000);
}

.Business_cooperation .privilege-page-a {
    background: url(../images/card.png) no-repeat #fff;
}

.Business_cooperation .privilege-page-a-card {
    margin-top: 22px;
    margin-left: 10px;
    font-size: 17px;
    float: left;
    overflow: hidden;
    width: 190px;
    height: 48px;
    padding: 1px;
    color: #fff;
    line-height: 25px;
    text-shadow: #996600 2px 1px 0px;
}

.Business_cooperation .privilege-page-a-qq {
    margin-top: 15px;
    margin-left: 20px;
    font-size: 18px;
    float: left;
    overflow: hidden;
    width: 150px;
    height: 50px;
    line-height: 18px;
    font-weight: 600;
    padding: 1px;
    color: #ef521d;
    text-shadow: #ab2b00 1px 1px 0px;
}

.Business_cooperation .privilege-item-mask {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: #000;
    filter: alpha(opacity=0);
    opacity: 0;
    -webkit-transition: opacity .2s ease-out;
    -moz-transition: opacity .2s ease-out;
    -o-transition: opacity .2s ease-out;
    transition: opacity .2s ease-out;
    transition-property: opacity;
    transition-duration: 0.2s;
    transition-timing-function: ease-out;
    transition-delay: 0s;
    color: white;
    font-size: 20px;
}

.Business_cooperation .privilege-page-list {
    margin: 15px -9px 0;
}

.Business_cooperation ol, .Business_cooperation ul {
    list-style: none;
}

.Business_cooperation .list {
    width: 300px;
    float: right;
    border: 1px solid #ddd;
    margin-top: 50px;
    margin-right: 10px;
}

.Business_cooperation body, .Business_cooperation dd, .Business_cooperation div, .Business_cooperation dl, .Business_cooperation dt, .Business_cooperation form, .Business_cooperation h1, .Business_cooperation h2, .Business_cooperation h3, .Business_cooperation h4, .Business_cooperation h5, .Business_cooperation h6, .Business_cooperation ol, .Business_cooperation p, .Business_cooperation ul {
    padding: 0;
    margin: 0;
}

.Business_cooperation .list_top {
    height: 43px;
    line-height: 43px;
    text-align: center;
}

.Business_cooperation .listtops {
    border-bottom: 1px solid #ddd;
    background-color: #fafbfc;
}

.Business_cooperation .list_Content {
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 14px;
    border-top: 1px solid #ddd;
}

.Business_cooperation .pm {
    width: 40px;
    float: left;
    height: 40px;
    line-height: 40px;
}

.Business_cooperation li {
    list-style-type: none;
}

.Business_cooperation li {
    display: list-item;
    text-align: -webkit-match-parent;
}

.Business_cooperation .nc {
    width: 156px;
    float: left;
    height: 40px;
    line-height: 40px;
    display: block;
    word-break: keep-all;
    white-space: nowrap;
    overflow: hidden;
    text-align: left;
}

.Business_cooperation .yl {
    width: 84px;
    float: right;
    color: #d21317;
    font-weight: bold;
}

.Business_cooperation .pm div {
    height: 28px;
    width: 15px;
    line-height: 15px;
    margin: auto;
    margin-top: 12px;
    color: #fff;
    font-size: 12px;
    font-weight: bold;
}

.Business_cooperation .privilege-item-mask {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: #000;
    opacity: 0;
    -webkit-transition: opacity .2s ease-out;
    -moz-transition: opacity .2s ease-out;
    -o-transition: opacity .2s ease-out;
    transition: opacity .2s ease-out
}

.Business_cooperation .privilege-item-des {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    line-height: 33px;
    text-align: center;
    background: #d7dde1;
    font-size: 16px;
    color: #464859;
    -webkit-transition: line-height .2s ease-out;
    -moz-transition: line-height .2s ease-out;
    -o-transition: line-height .2s ease-out;
    transition: line-height .2s ease-out
}

.Business_cooperation .privilege-item:hover .privilege-item-mask {
    opacity: .4;
    filter: alpha(opacity=40)
}
