.dashboard {
    margin-top: 10px !important;
    margin-bottom: 20px !important;
}

.dashboard .Tab_area {
    background-color: white;
    box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12) !important;
    border-radius: 5px;
    height: 500px;
}

.dashboard .Tab_area .MuiTabs-vertical {
    border: 1px solid #eee;
}

.dashboard .Tab_area .Tab_label {
    font-size: 15px;
    width: 100%;
    text-align: left;
    padding-left: 10px;
}

.dashboard .Tab_panel {
    background-color: white;
    margin-left: 12px;
    padding: 10px;
    padding-bottom: 20px;
    box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12) !important;
    float: right;
    border-radius: 5px;
}

.dashboard p {
    font-size: 15px;
}

.dashboard .membercentre {
    display: inline-flex;
}

.dashboard .membercentre .Tab_area .MuiTab-root {
    min-height: 30px;
}

.dashboard .membercentre .Tab_area .MuiTab-wrapper {
    text-align: left !important;
    justify-content: left !important;
    display: block;
}

.dashboard .Tab_area {
    border: 1px solid #eee;
    width: 200px !important;
    padding: 10px;
}

.dashboard .Tab_area_title {
    /* border-bottom: 1px solid #eee; */
    padding: 10px;
    display: inline-flex;
}

.dashboard .Tab_area_title h2 {
    /* margin: 0;
    padding: 0; */
    font: normal 700 19px/50px 'Microsoft Yahei', Arial, SimSun, '\5b8b\4f53', sans-serif;
    color: coral;
    text-align: center;
    margin-left: 10px !important;
    /* margin-left: 300px; */
}

.dashboard .Tab_area_title .MuiSvgIcon-root {
    margin-top: 10px;
}

.dashboard .Tab_area h2 {
    margin-left: 10px;
    margin: 0;
    padding: 0;
    font: normal 550 19px/40px 'Microsoft Yahei', Arial, SimSun, '\5b8b\4f53', sans-serif;
    /* color: coral; */
    text-align: center;
    /* margin-left: 3px; */
}

.dashboard .Tab_area_subtitle {
    /* background-color: ghostwhite; */
    text-align: left;
}

.dashboard .Tab_area_subtitle h2 {
    text-align: left;
    margin-left: 10px;
}

.dashboard .button_area {
    text-align: center;
    margin-top: 5px;
    margin-bottom: 5px;
    display: flex;
    justify-content: center;
}

.dashboard .Tab_area .MuiButton-label {
    font: normal 500 19px/25px 'Microsoft Yahei', Arial, SimSun, '\5b8b\4f53', sans-serif;
}

.dashboard .Tab_area .MuiButton-containedSecondary {
    background-color: #f56f4a;
}

/* .dashboard  .button_field .MuiButton-label {
    font: normal 500 18px/25px 'Microsoft Yahei', Arial, SimSun, '\5b8b\4f53', sans-serif;
}

.dashboard  .button_field .MuiButton-containedSecondary {
    background-color: #f56f4a;
} */

.dashboard .button_area .button_area_action {
    background-color: #f56f4a;
    color: white;
    font: normal 500 19px/25px 'Microsoft Yahei', Arial, SimSun, '\5b8b\4f53', sans-serif;
    text-decoration: none;
    margin-top: 50px;
    padding: 10px;
}

.membercentre table thead tr th {
    font-size: 18px;
    font-weight: 900;
}

.membercentre table tbody tr td {
    font-size: 15px;
}

.membercentre table tbody tr:nth-child(even) {
    background-color: #eee;
}

.membercentre .pagination {
    margin-top: 10px;
    justify-content: center;
}

.membercentre .Tab_Header {
    font-size: 18px !important;
    font-weight: 700;
    color: #f56f4a;
}