.recharge {
    margin-top: 10px !important;
    background-color: white;
    box-shadow: 0px 3px 5px -1px rgba(0,0,0,0.2), 0px 5px 8px 0px rgba(0,0,0,0.14), 0px 1px 14px 0px rgba(0,0,0,0.12) !important;
    margin-bottom: 20px;
    border-radius: 5px;
}

.recharge .recharge-row {
    display: inline-flex;
    width: 100%;
    margin-bottom: 20px;
}

.recharge .price {
    padding: 20px;
    width: 33.33%;
}

.recharge .shopheader{
    font-size: 22px;
    padding: 20px 20px 0px;
    margin: 0px;
    font-weight: 600;
}

.recharge .price .p_header {
    background: #FF9138;
    color: white;
    font-size: 3em;
    font-weight: 700;
    padding: 50px 20px;
    text-align: center;
    margin: 0px;
    border-radius: 10px;
    transition: all .2s ease-in-out;
    cursor: pointer;
    width: 100%;
}

.recharge .price .p_header:hover {
    transform: scale(1.05);
}

.recharge .price .p_title {
    /* background: #FEB63D; */
    color: black;
    text-align: center;
    font-size: 1.5em;
    padding: 10px;
    font-weight: 600;
    display: block;
    margin: auto;
    margin-bottom: 10px;
}

.recharge .price a {
    display: block;
    margin: auto;
    width: 45%;
    padding: 10px 0;
    font-size: 0.85em;
    color: #fff;
    background: #39B5FF;
    text-decoration: none;
    text-transform: uppercase;
    transition: all .3s ease-in-out;
    text-align: center;
    font-size: 16px;
    font-weight: 600;
}
  
.recharge .price a:hover {
    background: #018FFF;
    color: white;
    cursor: pointer;
}